import { useState } from "react";
import { flushSync } from "react-dom";
import QuizPage from "./Component/english_quiz/Component/QuizPage";
function App() {
  const [state, setState] = useState({});

  const changeQuestion = (
    data,
    isShowQuestion,
    showSolution,
    showCorrectIncorrect
  ) => {
    flushSync(() => {
      setState({});
    });
    setState({
      question_data: data,
      isShowQuestion: isShowQuestion || false,
      showSolution,
      showCorrectIncorrect,
    });
  };
  window.changeQuestion = changeQuestion;

  return (
    <>
      {Object.keys(state?.question_data || {}).length ? (
        <QuizPage
          obj={state?.question_data}
          isShowQuestion={state?.isShowQuestion || true}
          showSolution={state?.showSolution || false}
          showCorrectIncorrect={state?.showCorrectIncorrect || false}
        />
      ) : (
        ""
      )}
    </>
  );
}

export default App;
